
  $("#investments-table").DataTable({
    "serverSide": true,
    "ajax": "/datatable/inversiones",
    "responsive": true,
    "autoWidth": true,
    "paging": true,
    "lengthChange": true,
    "searching": true,
    "ordering": true,
    "info": true,
    "language" : {
      "url": "/lang/dataTables.es.json"
    },
    "columns":[
      {data: 'id'},
      {data: 'created_at'},
      {data: 'user_name'},
      {data: 'transaction_code'},
      {data: 'asset_type'},
      {data: 'value'},
      {data: 'balance'},
      {data: 'btn_delete'},
    ]
  });

  $(document).on("click", ".delete-investment", function(){
    var token = $("#token").val();
    var id = $(this).attr('data-investment');
    var route = "/inversion/"+id;

    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'DELETE',
      dataType: 'json',
      data: { },

      beforeSend:function(data){
        //$(".contentBtnApprove"+id).html('<div class="loader"></div>');
      },
      success:function(data){
        toastr.success(data['message'], "Notificación:");
        $("#investments-table").DataTable().ajax.url('/datatable/inversiones').load();
      },
      error:function(data){
        console.log('Error al realizar la consulta.');
      }
    });
  });

