//Pone dinamicamente puntos a los input tipo text
window.puntos = function(input){
  var num = input.value.replace(/\./g,'');
  if(!isNaN(num)){
    num = num.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g,'$1.');
    num = num.split('').reverse().join('').replace(/^[\.]/,'');
    input.value = num;
  }
}
//Valida que solo se ingrese numeros a los input tipo text
window.validarNumero = function(e){
  if(window.event){
    key = e.keyCode;
  }else if(e.which){
    key = e.which;
  }
  if ((key< 48 || key > 57) && key!=8){
    return false;
  }
}
