
  $("#requests-table").DataTable({
    "serverSide": true,
    "ajax": "/datatable/solicitudes",
    "responsive": true,
    "autoWidth": true,
    "paging": true,
    "lengthChange": true,
    "searching": true,
    "ordering": true,
    "info": true,
    "language" : {
      "url": "/lang/dataTables.es.json"
    },
    "columns":[
      {data: 'created_at'},
      {data: 'user_name'},
      {data: 'amount'},
      {data: 'bank'},
      {data: 'account_number'},
      {data: 'state'},
      {data: 'btn_approve'},
    ]
  });

  $(document).on("click", ".approve-request", function(){
    var token = $("#token").val();
    var id = $(this).attr('data-request');
    var state = $(this).attr('data-state');
    var route = "/aprueba-solicitud/"+id;

    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'POST',
      dataType: 'json',
      data: { state:state },

      beforeSend:function(data){
        //$(".contentBtnApprove"+id).html('<div class="loader"></div>');
      },
      success:function(data){
        toastr.success(data['message'], "Notificación:");
        $(".contentStateRequest"+id).html(data['state']);
        $("#requests-table").DataTable().ajax.url('/datatable/solicitudes').load();
      },
      error:function(data){
        console.log('Error al realizar la consulta.');
      }
    });
  });

