$("#trades-table").DataTable({
    "serverSide": true,
    "ajax": "/datatable/trades",
    "responsive": true,
    "autoWidth": true,
    "paging": true,
    "pageLength": 3,
    "lengthChange": false,
    "searching": false,
    "ordering": true,
    "info": true,
    "language" : {
      "url": "/lang/dataTables.es.json"
    },
    "columns":[
      {data: 'operation'},
      {data: 'asset'},
      {data: 'count'},
      {data: 'purchase_price'},
      {data: 'current_price'},
      {data: 'stop_lose'},
      {data: 'take_profit'},
      {data: 'profit'},
      {data: 'close'},
    ]
});

$("#asset").change(function(){
    const token = $("#token").val();
    const symbol = $(this).val();
    const route = "/get-exchange/"+symbol;
    
    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'GET',
      dataType: 'json',
      data: {},
      success:function(data){
        if(data){
            $("#priceAssetc").html('$'+data.open);
            $("#priceAssetv").html('$'+data.close);
        }else{
            $("#priceAssetc").html('---');
            $("#priceAssetv").html('---');
        }
      },
      error:function(data){
        console.log('Error al realizar la consulta.');
      }
    });
});

$(".trade_markets").click(function(e){
  e.preventDefault();
  const route = $(this).data('href');
  $.ajax({
    url: route,
    type: 'GET',
    dataType: 'html',
    data: {},
    beforeSend:function(){
      $("#home_content").html('Cargando ...');
    },
    success:function(data){
      if(data){
        $("#home_content").html(data);
      }
    },
    error:function(data){
      console.log('Error al realizar la consulta.');
    }
  });
});

$("#btn_open_trades").click(function(e){
  e.preventDefault();
  const token = $("#token").val();
  const route = $(this).data('action');
  const asset = $("#asset").val();
  const count = $("#count").val();

  if(asset && count){
    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'POST',
      dataType: 'json',
      data: { asset:asset, count:count },
      success:function(data){
        if(data.state){
          toastr.success(data.message, "Notificación:");
          $("#trades-table").DataTable().ajax.url('/datatable/trades').load();
          $("#count").val('');
          $("#asset").val('');
        }else{
          toastr.error(data.message, "Notificación:");
          $("#count").val('');
          $("#asset").val('');
        }
      },
      error:function(data){
        console.log('Error al realizar la consulta.');
      }
    });
  }else{
    toastr.info('Ingrese y seleccione los datos necesarios', "Notificación:");
  }
});

$(document).on('click', ".close_trade", function(e){
  e.preventDefault();
  const token = $("#token").val();
  const route = '/close-trade';
  const operation = $(this).data('operation');

  $.ajax({
    url: route,
    headers: {'X-CSRF-TOKEN':token},
    type: 'POST',
    dataType: 'json',
    data: { operation:operation },
    success:function(data){
      if(data.state){
        toastr.success(data.message, "Notificación:");
        $("#trades-table").DataTable().ajax.url('/datatable/trades').load();
      }else{
        toastr.error(data.message, "Notificación:");
      }
    },
    error:function(data){
      console.log('Error al realizar la consulta.');
    }
  });
});