  $(document).on('click', ".view-balance", function(){
    var token = $("#token").val();
    var id = $(this).attr('data-balance');
    var route = "/descripcion-de-balance/"+id;

    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'GET',
      dataType: 'json',
      data: { },

      success:function(data){
        $("#modalTitle").html("Descripción del balance");
        $("#modalBody").html(data);
      },
      error:function(data){
        console.log('Error al realizar la consulta.');
      }
    });
  });





