$("#client-agents-table").DataTable({
    "serverSide": true,
    "ajax": "/datatable/clients",
    "responsive": true,
    "autoWidth": true,
    "paging": true,
    "lengthChange": true,
    "searching": true,
    "ordering": true,
    "info": true,
    "language" : {
      "url": "/lang/dataTables.es.json"
    },
    "columns":[
      {data: 'created_at'},
      {data: 'name'},
      {data: 'contact'},
      {data: 'tracing'},
      {data: 'assign'},
    ]
});

$(document).on('click', ".view-client", function(){
    var token = $("#token").val();
    var id = $(this).attr('data-user');
    var route = "/form-asignacion/";

    $.ajax({
      url: route,
      headers: {'X-CSRF-TOKEN':token},
      type: 'GET',
      dataType: 'html',
      data: { id:id },
      success:function(data){
        $("#modalTitle").html("Asignación del cliente");
        $("#modalBody").html(data);
      },
      error:function(thrownError){
        console.log(thrownError);
        console.log('Error al realizar la consulta.');
      }
    });
});

$(document).on('click', "#formAssign", function(){
  var token = $("#token").val();
  var agent = $("#agent").val();
  var client = $("#client").val();
  var route = "/cliente-asignacion/";

  if(!agent){
    toastr.info('Es necesario seleccionar un agente.', "Notificación:");
    return;
  }

  $.ajax({
    url: route,
    headers: {'X-CSRF-TOKEN':token},
    type: 'POST',
    dataType: 'json',
    data: { agent:agent, client:client },
    success:function(data){
      toastr.success(data['message'], "Notificación:");
      $("#client-agents-table").DataTable().ajax.url('/datatable/clients').load();
      $("#modalApp").modal('hide');
    },
    error:function(thrownError){
      console.log(thrownError);
    }
  });
});

