$(document).on('click', "#formTracing", function(){
  var token = $("#token").val();
  var client = $("#client").val();
  var activity_performed = $("#activity_performed").val();
  var duration = '';
  var description = $("#description").val();
  var result = $("#result").val();
  var route = "/guarda-seguimiento/";

  if($("#days").val()){
    duration += $("#days").val()+' ';
  }if($("#hours").val()){
    duration += $("#hours").val()+' ';
  }if($("#minutes").val()){
    duration += $("#minutes").val();
  }

  if(!activity_performed || !duration || !description || !result){
    toastr.info('Es necesario llenar todo el formulario.', "Notificación:");
    return;
  }

  $.ajax({
    url: route,
    headers: {'X-CSRF-TOKEN':token},
    type: 'POST',
    dataType: 'json',
    data: { client:client, activity_performed:activity_performed, duration:duration, description:description, result:result },
    success:function(data){
      toastr.success(data['message'], "Notificación:");
      $("#modalApp").modal('hide');
    },
    error:function(thrownError){
      console.log(thrownError);
    }
  });
});

