$(document).on('click','#paginateTracing a', function(e){
	e.preventDefault();
	var page= $(this).attr('href').split('page=')[1];
  var id = $("#paginateTracing").attr('data-user');
	var route= "/historico-seguimiento";

	$.ajax({
		url: route,
		type: 'GET',
		dataType: 'html',
		data: { id:id, page:page },
    success:function(data){ 
      $("#modalBodyLg").html(data);
    },
    error:function(thrownError){
      console.log(thrownError);
      console.log('Error al realizar la consulta.');
    }
	});
});